}

.cards p{
    font-family: Poppins;
    font-size: 12px;
    font-weight: 300;
    line-height: 13px;
    letter-spacing: 0em;
    text-align: left;

}

.score {
    display: flex;
    justify-content: start;
    align-items: center;
}

.expImg {
    height: 235px;
    width: 176px;
    
    border-radius: 9px;

}

.starImg {
    height: 30px;
    width: 30px;
    padding: 0 10px;
}

.grayText {
    color: rgb(168, 168, 168);
}





@media screen and (max-width: 420px) {
  .hero {
    justify-content: flex-end;
  }

