/* Meme Generator */
.MemeHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: darkcyan;
  color: white;
  height: fit-content;
  padding: 10px 20px;

  box-shadow: 0 5px 5px rgba(128, 128, 128, 0.514);
}
.leftMeme {
  display: flex;
  justify-content: center;
  align-items: center;
}
.logoMeme {
  height: 7vh;
}
/* .headingMeme{} */
.rightMeme {
  display: flex;
  justify-content: center;
  align-items: center;
}
.projectHeadingMeme {
  font-weight: 500;
}
.MemeMain {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: fit-content;
  padding: 40px;
}

.formMeme {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: fit-content;
}

.textInput {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  width: fit-content;
  padding-top: 50px;
  padding-bottom: 20px;
  
}
#upperText, #lowerText {
  margin: 10px;
  padding: 10px 60px 10px 10px;

  font-size: 1em;

  border: solid;
  border-width: 1px;
  border-color: rgba(128, 128, 128, 0.486);
  border-radius: 10px;
  box-shadow: 1px 3px 5px rgba(128, 128, 128, 0.514);

}
#submitButton {
  padding: 10px;
  width: 97%;

  background-color: darkcyan;
  color: white;

  font-weight: 300;
  font-size: 1.2em;

  border-color: transparent;
  border-radius: 10px;
  box-shadow: 1px 3px 5px rgba(128, 128, 128, 0.514);

}

.meme {
  height: fit-content;
  max-width: 70vh;
  width: 100%;
  object-fit: cover;
  margin: 40px;
  border-radius: 20px;
  
}

@media screen and (max-width: 420px) {
  .MemeApp {
    min-width: none;
  }
  .MemeHeader {
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .MemeMain {
    height: 120vh;
  }

  #upperText, #lowerText, #submitButton {
    font-size: 1.5em;
  }
}

/* Photo shuffle */
.PhotoShuffle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  padding: 20px;
  background-color: rgb(247, 247, 247);
}

.photoShuffle {
  height: 40vh;
  border-radius: 20px;
  box-shadow: 1px 3px 5px rgba(128, 128, 128, 0.514);
}

.shuffleButton {
  margin: 20px 0 0 0;
  padding: 10px;


  font-weight: 300;
  font-size: 1.2em;

  border-color: transparent;
  border-radius: 10px;
  box-shadow: 1px 3px 5px rgba(128, 128, 128, 0.514);
}

/* state hello */
.stateHelloMain {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background-color: #262626;
  color: #D9D9D9;
  padding: 20px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.state {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.state--title {
  text-align: center;
}

.state--value {
  background-color: white;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #262626;
}


/* Counter */
.counterMain {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background-color: #262626;
  color: #D9D9D9;
  padding: 20px;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.counter {
  margin: 50px;
  display: flex;
  align-items: flex-end;
}

.counter > button {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  background-color: #737373;
  color: #D9D9D9;
  font-size: 1.5rem;
}

.counter > button:hover {
  background-color: #404040;
  color: #D9D9D9;
}

.counter > button:focus {
  outline: none;
}

.counter--count {
  background-color: white;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #262626;
}

.counter--plus {
  margin-left: -20px;
}

.counter--minus {
  margin-right: -20px;
  z-index: 1;
}

/* Favorite contact */
.card {
  width: 200px;
  border: 1px solid lightgray;
  border-radius: 10px;
  height: 350px;
  box-shadow: 5px 5px 5px 1px rgba(120,120,120,0.44);
  -webkit-box-shadow: 5px 5px 5px 1px rgba(120,120,120,0.44);
  -moz-box-shadow: 5px 5px 5px 1px rgba(120,120,120,0.44);
}

.card--image {
  width: 100%;
  padding: 10%;
  padding-bottom: 0;
}

.card--name {
  margin-block: 13px;
  color: #333333;
}

.card--info {
  padding: 10px;
}

.card--favorite {
  width: 25px;
  cursor: pointer;
}

.card--contact {
  font-size: 0.75rem;
  color: gray;
  margin-block: 7px;
}